import {getTransferedObjectUUID} from './classUtils';

const name = 'WebGLRenderBuffer';

function uuid(id) {
  return getTransferedObjectUUID(name, id);
}

export default class WebGLRenderbuffer {
  className = name;

  constructor(id) {
    this.id = id;
  }

  static uuid = uuid;

  uuid() {
    return uuid(this.id);
  }
}